@import url(https://fonts.googleapis.com/css2?family=Crimson+Text:ital,wght@1,400;1,600;1,700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Crimson+Text:ital,wght@1,400;1,600;1,700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Crimson+Text:ital,wght@1,400;1,600;1,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.NavbarListItemsDesktop ul {
  margin: 0;
  padding: 0;
}
.NavbarListItemsDesktop li {
  cursor: pointer;
  display: inline-block;
  margin: 20px;
}
.NavbarListItemsMobile {
  background-color: #822659;
  color: #f6f6f6;
}
.NavbarListItemsMobile li {
  cursor: pointer;
  display: flex;
  margin: 20px;
}
.Active {
  color: #3fc1c9;
}

.Intro {
  align-items: center;
  background-color: #3fc1c9;
  color: #f5f5f5;
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: calc(100vh + 53px);
  text-align: center;
}
.Text {
  margin: 0px 25px;
}
.Text h2 {
  font-size: 35px;
  line-height: 3.5rem;
}
.Text h2,
p {
  font-family: "Montserrat", sans-serif;
}
.Span {
  color: #822659;
  font-family: "Crimson Text", serif;
  font-size: 60px;
}
.Button {
  background-color: #3fc1c9;
  border: 5px solid #f6f6f6;
  cursor: pointer;
  width: 225px;
}
.Button:hover {
  background-color: #822659;
  transition: all 0.5s;
}
.Button:focus {
  outline: none;
}
.Button p {
  color: #f6f6f6;
  font-size: 15px;
}
.Link {
  display: flex;
  justify-content: space-evenly;
}
.Arrow {
  width: 20px;
  height: 20px;
  -webkit-filter: invert(100%) sepia(8%) saturate(80%) hue-rotate(107deg)
    brightness(115%) contrast(93%);
          filter: invert(100%) sepia(8%) saturate(80%) hue-rotate(107deg)
    brightness(115%) contrast(93%);
  position: relative;
  top: 15px;
  left: 3px;
}
section:nth-of-type(1) button .Arrow {
  transition: all 0.3s;
}
section:nth-of-type(1) button:hover .Arrow {
  transform: rotate(90deg);
  transition: all 0.3s;
}

.About {
  background-color: #f6f6f6;
  display: flex;
  justify-content: center;
  padding: 50px;
}
.About h1 {
  font-weight: bold;
  font-size: 35px;
  letter-spacing: 0.3rem;
  margin-bottom: 0;
  text-align: center;
}
.About h1,
h3 {
  color: #364f6b;
}
.About p {
  color: #364f6b;
  line-height: 2rem;
  padding: 10px;
}
.Wrap {
  display: flex;
  justify-content: space-between;
  text-align: center;
}
.Wrap img {
  border: 1px solid;
  border-radius: 100px;
  -webkit-filter: invert(27%) sepia(67%) saturate(308%) hue-rotate(170deg)
    brightness(93%) contrast(93%);
          filter: invert(27%) sepia(67%) saturate(308%) hue-rotate(170deg)
    brightness(93%) contrast(93%);
  width: 50px;
  padding: 10px;
}
.Offer {
  display: flex;
}
.Offer div {
  width: 100%;
  margin: 50px 15px;
}

@media (min-width: 280px) and (max-width: 950px) {
  .About p {
    margin: 0;
    text-align: center;
  }
  .Wrap {
    display: inline-block;
  }
}

.Chart_Bar__2lN-a {
  position: relative;
  padding: 25px;
  margin: auto;
  width: 65vw;
}

@media (min-width: 280px) and (max-width: 760px) {
  .Chart_Bar__2lN-a {
    position: relative;
    padding: 25px;
    margin: auto;
    width: 86vw;
  }
}

.Wrapper {
  background-color: #ebebeb;
  box-shadow: 0px 10px 15px 0px #000000;
  color: #364f6b;
  display: flex;
  justify-content: center;
  padding: 50px 0 100px;
}
.Wrapper h1 {
  font-weight: bold;
  font-size: 35px;
  letter-spacing: 0.3rem;
  text-align: center;
}
.Projects {
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
}

.Project {
  margin: 5px;
}
.Project h3 {
  color: #f6f6f6;
}
.ProjectOverLay {
  display: hidden;
  background: transparent;
  height: 100%;
  width: 100%;
  opacity: 0;
  transition: all 600ms ease-in-out;
  position: relative;
  width: 350px;
  height: 240px;
  top: -20px;
  left: -50px;
  padding: 25px;
}
.ProjectOverLay:hover {
  visibility: visible;
  opacity: 1;
  background: #364f6b;
  transform: scale(1.1);
  transition: all 300ms ease-in-out;
}

.ProjectLinks {
  display: flex;
  justify-content: center;
}
.ProjectLinks a {
  border: 2px solid #f6f6f6;
  color: #f6f6f6;
  display: flex;
  font-size: 16px;
  font-weight: 600;
  justify-content: center;
  margin: 10px;
  padding: 10px;
  text-decoration: none;
  transition: 600ms ease-in-out;
  width: 150px;
}
.ProjectLinks a:hover {
  background-color: #822659;
  color: #f6f6f6;
  transition: 300ms ease-in-out;
}

.TopBox {
  width: 100%;
  height: 10px;
}

.Footer {
  align-items: center;
  background-color: #364f6b;
  color: #f6f6f6;
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  padding: 25px;
}
.Footer p {
  font-family: "Montserrat", sans-serif;

  letter-spacing: 0.1rem;
}
.Footer span {
  color: #3fc1c9;
  font-weight: 600;
}
.Icon img {
  cursor: pointer;
  -webkit-filter: invert(81%) sepia(17%) saturate(1701%) hue-rotate(132deg)
    brightness(85%) contrast(83%);
          filter: invert(81%) sepia(17%) saturate(1701%) hue-rotate(132deg)
    brightness(85%) contrast(83%);
  margin: 15px;
  width: 40px;
}
.Icon img:hover {
  -webkit-filter: invert(50%) sepia(45%) saturate(7246%) hue-rotate(167deg)
    brightness(87%) contrast(83%);
          filter: invert(50%) sepia(45%) saturate(7246%) hue-rotate(167deg)
    brightness(87%) contrast(83%);
}

h1 {
  font-family: "Crimson Text", serif;
}

h2,
h3,
p {
  font-family: "Montserrat", sans-serif;
}
nav {
  font-family: "Montserrat", sans-serif;
  font-size: 15px;
}
span {
  font-family: "Crimson Text", serif;
}

